.prepaid-report {
  font-family: Helvetica, sans-serif;
  font-size: 15px;
}

.prepaid-report-header {
  border: 2px solid black;
  border-radius: 15px;
  padding: 3px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 8px;
}

.prepaid-report-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 11px;
  margin-top: 10px;
}

.invisible-cell {
  border: none !important;
  background-color: white !important;
}

.prepaid-report-header-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.prepaid-report-table {
  font-family: Helvetica, sans-serif;
  font-size: 11px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  border-collapse: collapse;
}

.prepaid-report-thead > th {
  border: 1px solid black;
  font-weight: normal;
  border-collapse: collapse;
}

.prepaid-report-tbody td {
  border: 1px solid black;
  font-weight: normal;
  text-align: right;
  box-sizing: border-box;
  border-collapse: collapse;
}

.prepaid-report-logo {
  position: absolute;
  height: 60px;
  margin-left: auto;
  left: 0;
  margin-right: auto;
  right: 0;
}
