import { Component, Host, Prop, h } from '@stencil/core';
import debug from 'debug';
import { StringUtils } from '../../utils/string';
import { getWeek } from '../../utils/utils';
import { DriversStruct, GeneratedAtStruct, PeriodsStruct } from './interfaces/prepaidReport.interface';

@Component({
  tag: 's3p-prepaid-report',
  styleUrl: 's3p-prepaid-report.css',
  shadow: false,
})
export class S3pPrepaidReport {
  private readonly log: debug.Debugger = debug('S3pPrepaidReport');

  /** Current driver for the page. */
  @Prop() driver: DriversStruct;
  /** Month of the prepaid-report. */
  @Prop() month: number;
  /** Year of the prepaid-report. */
  @Prop() year: number;
  /** Information about generation date and time of the prepaid-report. */
  @Prop() generatedAt: GeneratedAtStruct;
  /** Page number of the current page. */
  @Prop() currentPage: number;
  /** Number of total pages. */
  @Prop() totalPage: number;

  private locale: string = 'fr-FR';
  private start: Date;
  private stop: Date;

  /** Init of the prepaid report. */
  async componentWillLoad() {
    if (this.driver == null || this.month == null || this.year == null) {
      this.log('You must set all props.');
      return;
    }

    this.start = new Date();
    this.start.setUTCHours(0, 0, 0, 0);
    this.start.setUTCFullYear(this.year, this.month - 1, 1);

    this.stop = new Date();
    this.stop.setUTCHours(23, 59, 59, 999);
    this.stop.setUTCFullYear(this.year, this.month, 0);

    // Add missing days
    const templateTs: number[] = Array.from({ length: this.stop.getUTCDate() }, (_, i) => i + 1);
    const periodTs: number[] = this.driver.periods.map(period => new Date(period.ts).getDate());
    const missingDays: number[] = templateTs.filter(x => !periodTs.includes(x));
    for (const missingDay of missingDays) {
      const ts = new Date();
      ts.setUTCFullYear(this.year, this.month - 1, missingDay);
      this.driver.periods.push(this.generateMissingDay(ts));
    }
    // Sort periods
    this.driver.periods = this.driver.periods.sort((a, b) => Date.parse(a.ts) - Date.parse(b.ts));

    // Groups by week number
    const weeks = this.driver.periods.reduce((weeks, item) => {
      const weekNumber: number = getWeek(new Date(item.ts));
      const week = weeks[weekNumber] || [];
      week.push(item);
      weeks[weekNumber] = week;
      return weeks;
    }, {});

    // Generate month and period summary
    const monthSummary = this.generateMonthSummary();
    const periodSummary = this.generatePeriodSummary();

    // Add week summary
    for (const [weekNumber, week] of Object.entries<PeriodsStruct[]>(weeks)) {
      const lastDayOfWeek = week[week.length - 1];
      const index = this.driver.periods.findIndex(period => period.ts === lastDayOfWeek.ts);
      // Add the week summary after the end of the week
      if (index > -1) {
        this.driver.periods.splice(index + 1, 0, this.generateWeekSummary(Number.parseInt(weekNumber), week));
      }
    }

    // Add month summary
    this.driver.periods.push(monthSummary);
    // Add period summary
    this.driver.periods.push(periodSummary);
  }

  componentDidLoad() {
    const div: HTMLDivElement = document.createElement<'div'>('div');
    div.id = 'waiting';
    document.body.appendChild(div);
  }

  /**
   * Generate the month summary for the current driver.
   * @returns {PeriodsStruct} A month summary in period.
   */
  private generateMonthSummary(): PeriodsStruct {
    const month: Date = new Date(this.year, this.month - 1);

    const monthSummary: PeriodsStruct = {
      ts: StringUtils.toTitleCase(month.toLocaleString(this.locale, { month: 'long' })),
      _tsType: 'string',
      kms: 0,
      absNR: null,
      absRem: null,
      available: null,
      availableDE: null,
      driving: null,
      end: null,
      expenses: {
        bonus: null,
        evening: null,
        lunch: null,
        morning: null,
        night: null,
        total: null,
        unique: null,
      },
      hdRem: null,
      matAM: null,
      night: null,
      service: null,
      serviceGlobal: null,
      start: null,
      working: null,
    };

    // Make the sums of period on the week
    for (const period of this.driver.periods) {
      monthSummary.driving += period.driving;
      monthSummary.working += period.working;
      monthSummary.available += period.available;
      // monthSummary.availableDE += period.availableDE;
      monthSummary.service += period.service;
      // monthSummary.hdRem += period.hdRem;
      // monthSummary.absRem += period.absRem;
      // monthSummary.absNR += period.absNR;
      monthSummary.night += period.night;
      monthSummary.kms += period.kms;
      monthSummary.serviceGlobal += period.serviceGlobal;
      monthSummary.expenses.bonus += period.expenses.bonus;
      monthSummary.expenses.evening += period.expenses.evening;
      monthSummary.expenses.lunch += period.expenses.lunch;
      monthSummary.expenses.morning += period.expenses.morning;
      monthSummary.expenses.night += period.expenses.night;
      monthSummary.expenses.total += period.expenses.total;
      monthSummary.expenses.unique += period.expenses.unique;
    }

    return monthSummary;
  }

  /**
   * Generate the period summary for the current driver.
   * @returns {PeriodsStruct} A period summary in period.
   */
  private generatePeriodSummary(): PeriodsStruct {
    const periodSummary: PeriodsStruct = {
      ts: 'Période',
      _tsType: 'string',
      kms: 0,
      absNR: null,
      absRem: null,
      available: null,
      availableDE: null,
      driving: null,
      end: null,
      expenses: {
        bonus: null,
        evening: null,
        lunch: null,
        morning: null,
        night: null,
        total: null,
        unique: null,
      },
      hdRem: null,
      matAM: null,
      night: null,
      service: null,
      serviceGlobal: null,
      start: null,
      working: null,
    };

    // Make the sums of period on the week
    for (const period of this.driver.periods) {
      periodSummary.driving += period.driving;
      periodSummary.working += period.working;
      periodSummary.available += period.available;
      // periodSummary.availableDE += period.availableDE;
      periodSummary.service += period.service;
      // periodSummary.hdRem += period.hdRem;
      // periodSummary.absRem += period.absRem;
      // periodSummary.absNR += period.absNR;
      periodSummary.night += period.night;
      periodSummary.kms += period.kms;
      periodSummary.serviceGlobal += period.serviceGlobal;
      periodSummary.expenses.bonus += period.expenses.bonus;
      periodSummary.expenses.evening += period.expenses.evening;
      periodSummary.expenses.lunch += period.expenses.lunch;
      periodSummary.expenses.morning += period.expenses.morning;
      periodSummary.expenses.night += period.expenses.night;
      periodSummary.expenses.total += period.expenses.total;
      periodSummary.expenses.unique += period.expenses.unique;
    }

    return periodSummary;
  }

  /**
   * Generate a week summary on period format.
   * @param {number} weekNumber Nth week of the year.
   * @param {PeriodsStruct[]} periods Periods for the current week.
   * @returns {PeriodsStruct} A week summary in period.
   */
  private generateWeekSummary(weekNumber: number, periods: PeriodsStruct[]): PeriodsStruct {
    const weekSummary: PeriodsStruct = {
      ts: `Semaine ${weekNumber.toString().padStart(2, '0')}`,
      _tsType: 'string',
      kms: 0,
      absNR: null,
      absRem: null,
      available: null,
      availableDE: null,
      driving: null,
      end: null,
      expenses: {
        bonus: null,
        evening: null,
        lunch: null,
        morning: null,
        night: null,
        total: null,
        unique: null,
      },
      hdRem: null,
      matAM: null,
      night: null,
      service: null,
      serviceGlobal: null,
      start: null,
      working: null,
    };

    // Make the sums of period on the week
    for (const period of periods) {
      weekSummary.driving += period.driving;
      weekSummary.working += period.working;
      weekSummary.available += period.available;
      // weekSummary.availableDE += period.availableDE;
      weekSummary.service += period.service;
      // weekSummary.hdRem += period.hdRem;
      // weekSummary.absRem += period.absRem;
      // weekSummary.absNR += period.absNR;
      weekSummary.night += period.night;
      weekSummary.kms += period.kms;
      weekSummary.serviceGlobal += period.serviceGlobal;
      weekSummary.expenses.bonus += period.expenses.bonus;
      weekSummary.expenses.evening += period.expenses.evening;
      weekSummary.expenses.lunch += period.expenses.lunch;
      weekSummary.expenses.morning += period.expenses.morning;
      weekSummary.expenses.night += period.expenses.night;
      weekSummary.expenses.total += period.expenses.total;
      weekSummary.expenses.unique += period.expenses.unique;
    }

    return weekSummary;
  }

  /**
   * Create a missing day with no values.
   * @param {Date} ts Timestamp of the missing day.
   * @returns {PeriodsStruct} A missing day in period.
   */
  private generateMissingDay(ts: Date): PeriodsStruct {
    return {
      ts: ts.toISOString().split('T')[0],
      _tsType: 'date',
      kms: 0,
      absNR: null,
      absRem: null,
      available: null,
      availableDE: null,
      driving: null,
      end: null,
      expenses: {
        bonus: null,
        evening: null,
        lunch: null,
        morning: null,
        night: null,
        total: null,
        unique: null,
      },
      hdRem: null,
      matAM: null,
      night: null,
      service: null,
      serviceGlobal: null,
      start: null,
      working: null,
    };
  }

  /**
   * Get the color of the row concording to its ts.
   * @param {string} ts String for the ts.
   * @returns {string} Color for the row.
   */
  private getRowColor(ts: string): string {
    if (ts.includes('Semaine')) {
      return '#e2e2fa';
    } else if (ts.includes('Période')) {
      return '#9393fb';
    } else {
      return '#c5c5fb';
    }
  }

  render() {
    return (
      <Host class="prepaid-report">
        <header class="prepaid-report-header">
          {this.driver.logo != null ? <img class="prepaid-report-logo" src={this.driver.logo}></img> : null}
          <div class="prepaid-report-header-line">
            <span>
              Société : {this.driver.legalStructure ?? 'N/A'} - {this.driver.siret ?? 'N/A'}
            </span>
            <span>
              <b>Synthèse des disques et frais</b>
            </span>
          </div>
          <div class="prepaid-report-header-line">
            <span>
              Conducteur : {this.driver.lastName.toUpperCase()} {this.driver.firstName.toUpperCase()}
            </span>
            <span>
              <b>
                Du {this.start?.toLocaleDateString(this.locale, { timeZone: 'UTC' })} au {this.stop?.toLocaleDateString(this.locale, { timeZone: 'UTC' })}
              </b>
            </span>
          </div>
          <div class="prepaid-report-header-line">
            <span>Matricule : {this.driver.socialExternalId ?? 'N/A'}</span>
          </div>
        </header>

        <main>
          <table class="prepaid-report-table" cellspacing="0">
            <thead class="prepaid-report-thead">
              <th class="invisible-cell"></th>
              <th>Début</th>
              <th>Fin</th>
              <th>Conduite</th>
              <th>Travail</th>
              <th>Dispo</th>
              <th>Disp DE</th>
              <th>Service</th>
              <th>HD rem</th>
              <th>Abs rem</th>
              <th>Abs nr</th>
              <th>Mat/AM</th>
              <th>Nuit</th>
              <th>Kms</th>
              <th>Serv global</th>
              <th class="invisible-cell"></th>
              <th>Pt. D</th>
              <th>Déj</th>
              <th>Dîner</th>
              <th>Nuit</th>
              <th>Divers</th>
              <th>Total</th>
              <th>Prime</th>
            </thead>
            <tbody class="prepaid-report-tbody">
              {this.driver.periods.map(period => {
                const ts = new Date(period.ts);

                // Do not display 0 values
                if (period.available === 0) {
                  period.available = null;
                }

                if (period.night === 0) {
                  period.night = null;
                }

                // if (period.kms === 0) {
                //   period.kms = null;
                // }

                if (period.expenses.morning === 0) {
                  period.expenses.morning = null;
                }

                if (period.expenses.bonus === 0) {
                  period.expenses.bonus = null;
                }

                if (period.expenses.evening === 0) {
                  period.expenses.evening = null;
                }

                if (period.expenses.lunch === 0) {
                  period.expenses.lunch = null;
                }

                if (period.expenses.night === 0) {
                  period.expenses.night = null;
                }

                if (period.expenses.night === 0) {
                  period.expenses.night = null;
                }

                if (period.expenses.unique === 0) {
                  period.expenses.unique = null;
                }

                if (period.expenses.total === 0) {
                  period.expenses.total = null;
                }

                return (
                  <tr style={{ backgroundColor: period._tsType === 'string' ? this.getRowColor(period.ts) : 'none' }}>
                    <td>
                      {period._tsType === 'string'
                        ? period.ts
                        : ts.toLocaleDateString(this.locale, { timeZone: 'UTC', weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric' })}
                    </td>
                    <td>{period.start}</td>
                    <td>{period.end}</td>
                    <td>{period.driving?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.working?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.available?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.availableDE}</td>
                    <td>{period.service?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.hdRem}</td>
                    <td>{period.absRem}</td>
                    <td>{period.absNR}</td>
                    <td>{period.matAM}</td>
                    <td>{period.night?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.kms}</td>
                    <td>{period.serviceGlobal?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td class="invisible-cell"></td>
                    <td>{period.expenses.morning}</td>
                    <td>{period.expenses.lunch}</td>
                    <td>{period.expenses.evening}</td>
                    <td>{period.expenses.night}</td>
                    <td>{period.expenses.unique}</td>
                    <td>{period.expenses.total?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                    <td>{period.expenses.bonus?.toLocaleString(this.locale, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </main>

        <footer class="prepaid-report-footer">
          <span>
            {/* <i>Généré le 19/04/2023 16:03:49</i> */}
            <i>
              Généré le {this.generatedAt.date} {this.generatedAt.time}
            </i>
          </span>
          <span>
            {/* <i>Page 1/2</i> */}
            <i>
              Page {(this.currentPage + 1).toString()}/{this.totalPage.toString()}
            </i>
          </span>
        </footer>
      </Host>
    );
  }
}
